import * as React from "react";
import Header from "../components/header";
import { Link, withPrefix } from "gatsby";

export default function Home() {
  return (
    <div>
      <Header title="Home Header !!"></Header>
      <div style={{ color: `purple`, fontSize: `72px` }}>Hello world! 05:33</div>
      <img src="https://picsum.photos/300/200" alt="000" />
      <hr />
      <div>
        <Link to={withPrefix("/about/")}>About</Link>
      </div>
      <div>
        <Link to={withPrefix("/secondpage/")}>SecondPage</Link>
      </div>
      <div>
        <Link to={withPrefix("/contact/")}>Contact</Link>
      </div>
    </div>
  );
}
